/**
 * Embedded Wallet settings page - enable/disable and configure recovery
 */
import { useEffect } from 'react';
import { LoadingSpinner } from '../../shared/LoadingSpinner';
import { ErrorMessage } from '../../shared/ErrorMessage';
import { useSettingsAutosave } from '../../../hooks/useSettingsAutosave';
import { SettingsSection } from './settingsInputs';
import { AutosaveStatus } from './AutosaveStatus';

/** Keys to show on this page (excluding the master toggle which is in header) */
const SETTINGS_KEYS = ['wallet_recovery_mode'];

export interface EmbeddedWalletSettingsProps {
  className?: string;
}

export function EmbeddedWalletSettings({ className }: EmbeddedWalletSettingsProps) {
  const {
    settings,
    edits,
    isLoading,
    autosaveStatus,
    autosaveError,
    error,
    fetchSettings,
    handleChange,
    getEffectiveValue,
  } = useSettingsAutosave();

  useEffect(() => {
    fetchSettings();
  }, [fetchSettings]);

  // Get wallet settings from features category
  const featuresSettings = settings['features'] ?? [];
  const currentSettings = featuresSettings
    .filter((s) => SETTINGS_KEYS.includes(s.key))
    .sort((a, b) => SETTINGS_KEYS.indexOf(a.key) - SETTINGS_KEYS.indexOf(b.key));

  // Embedded wallet master toggle
  const walletEnabled = getEffectiveValue('feature_wallet_signing') === 'true';
  const handleWalletToggle = () => {
    handleChange('feature_wallet_signing', walletEnabled ? 'false' : 'true');
  };

  // User withdrawals toggle (only visible when wallet is enabled)
  const withdrawalsEnabled = getEffectiveValue('feature_user_withdrawals') === 'true';
  const handleWithdrawalsToggle = () => {
    handleChange('feature_user_withdrawals', withdrawalsEnabled ? 'false' : 'true');
  };

  if (isLoading && Object.keys(settings).length === 0) {
    return (
      <div className={`cedros-system-settings cedros-system-settings-loading ${className ?? ''}`}>
        <LoadingSpinner />
        <span>Loading settings...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`cedros-system-settings ${className ?? ''}`}>
        <ErrorMessage error={error.message} />
      </div>
    );
  }

  return (
    <div className={`cedros-system-settings ${className ?? ''}`}>
      <div className="cedros-settings-page-header">
        <div className="cedros-settings-page-header-content">
          <h2 className="cedros-settings-page-title">Embedded Wallet</h2>
          <p className="cedros-settings-page-description">
            Configure the embedded wallet for transaction signing.
          </p>
        </div>
        <div className="cedros-settings-page-header-actions">
          <button
            type="button"
            role="switch"
            aria-checked={walletEnabled}
            className={`cedros-toggle ${walletEnabled ? 'cedros-toggle-on' : 'cedros-toggle-off'}`}
            onClick={handleWalletToggle}
          >
            <span className="cedros-toggle-track">
              <span className="cedros-toggle-thumb" />
            </span>
            <span className="cedros-toggle-label">{walletEnabled ? 'Enabled' : 'Disabled'}</span>
          </button>
          <AutosaveStatus status={autosaveStatus} error={autosaveError} />
        </div>
      </div>

      {/* User Withdrawals toggle (only visible when wallet is enabled) */}
      {walletEnabled && (
        <div className="cedros-settings-subsection">
          <div className="cedros-settings-subsection-header">
            <div>
              <h3 className="cedros-settings-subsection-title">User Withdrawals</h3>
              <p className="cedros-settings-subsection-description">
                Allow users to withdraw SOL/SPL tokens from their embedded wallet to external
                addresses.
              </p>
            </div>
            <button
              type="button"
              role="switch"
              aria-checked={withdrawalsEnabled}
              className={`cedros-toggle ${withdrawalsEnabled ? 'cedros-toggle-on' : 'cedros-toggle-off'}`}
              onClick={handleWithdrawalsToggle}
            >
              <span className="cedros-toggle-track">
                <span className="cedros-toggle-thumb" />
              </span>
              <span className="cedros-toggle-label">
                {withdrawalsEnabled ? 'Enabled' : 'Disabled'}
              </span>
            </button>
          </div>
        </div>
      )}

      {/* Settings content */}
      <div className="cedros-admin-tab-content">
        {currentSettings.length === 0 ? (
          <div className="cedros-system-settings-empty">
            <p>No additional settings available.</p>
          </div>
        ) : (
          <SettingsSection settings={currentSettings} edits={edits} onChange={handleChange} />
        )}
      </div>
    </div>
  );
}
